html {
    margin:  0;
    padding: 0;
    height: 100vh;
    background: url("../public/images/background.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat; 
    text-align: 'center';
  }

  .overlay {
    align-items: center;
    position: absolute;
    top: 0;
    left: 20vw;
    width: 60vw;
    height: 100%;
    background-color: rgba(195, 43, 226, 0.6); /* Use rgba to set the color with transparency */
    border-radius: 1%;
    box-shadow: inset 1px 1px 20px 5px #ffffff;;
    overflow: scroll;
  }

  ::-webkit-scrollbar{
    display:none;
  }
  