.text{
    height: auto;
    padding: 10px;
    width: 90%;
    margin-left: 50px;
    margin-right: 50px;
    background-color: rgb(236, 213, 183);
    border-radius: 10px;/* Радіус закруглення 10px для всіх кутів блока */
    text-align: center;
  }

  .correct{
    height: auto;
    padding: 10px;
    width: 90%;
    margin-left: 5%;
    background-color: rgb(6, 191, 18);
    color: white;
    text-align: center;
    border-radius: 1%;
  }

  .incorrect{
    height: auto;
    padding: 10px;
    width: 90%;
    margin-left: 5%;
    background-color: rgb(238, 4, 4);
    color: white;
    text-align: center;
    border-radius: 1%;
  }

  button {
    background-color: rgb(236, 213, 183);
    margin-bottom: 10px; /* Додано відступ між кнопками */
    width: 650px;
    height: 90px;
    
    border: none;
    font-size: 1.2rem;
    border-radius: 10px;/* Радіус закруглення 10px для всіх кутів блока */
  }

  